* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  height: 100vh;
  color: #fff;
  flex-direction: column;
  align-items: center;
  font-family: "Exo 2", sans-serif;
  font-size: 1.5rem;
  display: flex;
  overflow: hidden;
}

#top-container {
  width: 200px;
  max-width: 95%;
  z-index: 1;
  justify-content: space-between;
  align-items: center;
  display: flex;
  position: absolute;
  top: 5vh;
}

#top-container .mode-btn {
  cursor: pointer;
  opacity: .5;
  border: none;
  border-radius: 1rem;
  padding: .75rem;
  font-family: inherit;
  font-size: 1.25rem;
  box-shadow: .1rem .1rem 24px 4px #0003;
}

#top-container .mode-btn:active {
  transform: scale(.9);
}

#top-container .mode-btn.active {
  opacity: 1;
}

#granim-canvas {
  height: 100vh;
  width: 100vw;
  position: absolute;
  overflow: hidden;
}

#circle-container {
  height: 350px;
  width: 350px;
  justify-content: center;
  align-items: center;
  margin: auto;
  display: flex;
  position: relative;
  transform: scale(1);
}

#outer-circle {
  background: var(--gradient0);
  height: 100%;
  width: 100%;
  z-index: -2;
  border-radius: 50%;
  position: absolute;
}

#inner-circle {
  height: 100%;
  width: 100%;
  z-index: -1;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

#pointer {
  height: 1.5rem;
  width: 1.5rem;
  background-color: #fff;
  border-radius: 50%;
  display: block;
}

#pointer-container {
  width: 20px;
  height: 70%;
  transform-origin: bottom;
  position: absolute;
  transform: translateY(-50%);
}

.footer {
  width: 80vw;
  max-width: 95%;
  justify-content: space-between;
  align-items: center;
  display: flex;
  position: absolute;
  bottom: 0;
}

.color-controls-container {
  cursor: pointer;
  width: 180px;
  height: 36px;
  z-index: 3;
  background-color: #fff;
  border-radius: 16px;
  justify-content: space-evenly;
  align-items: center;
  transition: all .2s;
  display: flex;
  position: relative;
  bottom: 1.5rem;
  left: 0;
  box-shadow: .1rem .1rem 24px 4px #0003;
}

.color-controls-container.folded {
  width: 36px;
  border-radius: 16px;
}

.color-controls-container.folded .color-scheme {
  display: none;
  position: absolute;
}

.color-controls-container.folded .color-scheme.active {
  display: block;
}

.color-controls-container .color-scheme {
  width: 24px;
  height: 24px;
  border-radius: 50%;
  transition: all .2s;
  display: block;
}

.animation-controls {
  justify-content: center;
  align-items: center;
  display: flex;
  position: relative;
  bottom: 1.5rem;
  right: 50%;
  transform: translateX(50%);
}

#animation-control-btn {
  width: 36px;
  height: 36px;
  cursor: pointer;
  border: none;
  border-radius: 50%;
}

#animation-control-btn:active {
  transform: scale(.9);
}

#animation-control-btn i {
  font-size: 1.25rem;
}

#animation-control-btn i.fa-play {
  margin-left: 3px;
}

.soundbutton {
  cursor: pointer;
  width: 36px;
  height: 36px;
  background-color: #fff;
  border-radius: 16px;
  justify-content: space-evenly;
  align-items: center;
  transition: all .25s;
  display: flex;
  position: absolute;
  bottom: 1.5rem;
  right: 0;
}

.soundbutton.paused {
  opacity: .5;
}

.soundbutton img {
  width: 70%;
  height: 70%;
}

@media (max-width: 600px) {
  #circle-container {
    width: 250px;
    height: 250px;
  }
}

@media (max-width: 300px) {
  #circle-container {
    width: 125px;
    height: 125px;
  }

  #pointer {
    width: 10px;
    height: 10px;
  }
}

